$main-color:  #173F5F;
$submit-green: rgb(125, 193, 23);
$light-grey: rgb(191, 185, 185);

.close-btn{
    position: absolute;
    right: 20px;
    top: 0;
    color: red;
    font-size: 30px;
}
.submit-btn{
    width: 200px;
    background-color: $submit-green;
    color: white;
    border: none;
    border-radius: 10px;
    font-size: 20px;
    margin: 20px auto;
}

.container-change{
    display: flex;
    flex-direction: column;
    justify-content: start;
    align-content: flex-start;
}


.change-input-wrapper{
    position: relative;
    margin: 0;
}

.input-change{
    border: 1px solid $light-grey;
    padding-left: 20px;
    width: 100%;
    height: 50px;   
    border-radius: 10px;
    margin: 10px 0;
    &:focus{
        outline: none;
    }
}

.change-label{
    position: absolute;
    top: 25px;
    left: 20px;
    margin: 0;
    transition: .2s ease-in-out;
}

.change-label-active {
    top: 15px;
    font-size: 10px;
}

@media (min-width: 640px) {

    .change-input-wrapper{
        position: relative;
        margin: 10px 0;
    }

    .input-change{
        border: 1px solid $light-grey;
        padding-left: 20px;
        width: 500px;
        height: 50px;   
        border-radius: 10px;
        margin: 10px 0;
        &:focus{
            outline: none;
        }
    }
}

@media (max-width: 820px){
    .input-change{
        border: 1px solid $light-grey;
        padding-left: 20px;
        width: 100%;
        height: 50px;   
        border-radius: 10px;
        margin: 10px 0;
        &:focus{
            outline: none;
        }
    }
}
 @media (min-width: 960px){
     .container-change{
         display: block;
         margin: 15px;
     }

    .input-change{
        display: block;
        &:focus{
            outline: none;
        }
    }

    .change-label{
        position: absolute;
        top: 16px;
        left: 20px;
        margin: 0;
        transition: .2s ease-in-out;
    }

    .change-label-active {
        top: 5px;
        font-size: 10px;
    }
 }